import "./Expenses.css";
import React, { useState } from 'react';
import Card from "../UI/Card";
import ExpenseItem from "./ExpenseItem";
import TotalExpense from "./TotalExpense";
import PieChart from "../UI/PieChart";

const Expenses = (props) => {

  const SaveInExpenses = (deleteExpense) => {
    props.FromAppToExpense(deleteExpense);
  }

  let expenseCardClasses = 'expenses ex-' + props.ExpensesTheme;
  // console.log( props.totalExpense);
  // console.log( props.totalIncome);
     if(props.totalIncome!=0 || props.totalExpense!=0)
     return (
      <Card className={expenseCardClasses}>
        <PieChart expenseAmount={props.totalExpense} incomeAmount={props.totalIncome} />
        <TotalExpense title='Total Income' amount={props.totalIncome} />
        <TotalExpense title='Total Expense' amount={props.totalExpense} />

        {
          props.Expenses.map(
            expense => (<ExpenseItem ExpenseItem={expense}
              FromExpenses={SaveInExpenses} ItemTheme={props.ExpensesTheme}
            />)
          )

        } </Card>


    );
}
export default Expenses;
