import React, { useState,useEffect } from 'react';
import FilterExpenses from './Expenses/FilterExpenses';
import FilterByDate from './FilterByDate';
import Theme from './UI/Theme';
import "./Header.css";

const Header = (props) => {
  const [HeaderTheme, setHeaderTheme] = useState('');
  
  

 
 useEffect(() => {
    
  props.defaultTheme==='dark-theme' ?
      setHeaderTheme("dark-theme") : setHeaderTheme("light-theme");
 }, [props.defaultTheme])
 

  const FilterAndSaveInHeader = (filterExpenseName) => {
    props.FromAppForFilter(filterExpenseName);
  }


  const SaveThemeInHeader = () => {
    // HeaderTheme === "light-theme" ?
    //   setHeaderTheme("dark-theme") : setHeaderTheme("light-theme");
    props.FromApp();
  }
  const FilterDateAndSaveInHeader = (s, e) => {
    props.FromAppForDateFilter(s, e);
  }

  if (HeaderTheme == "light-theme") {
  
    return (
      <nav className="navbar navbar-expand-lg fixed-top lightNavbar  ">
        <div className="container-fluid">
          <a className="navbar-brand" href="#">Kharcha-Pani Tracker</a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item mx-2">
                <a className="nav-link active" aria-current="page" href="#">Home</a>
              </li>
              <li className="nav-item">
                <a className="nav-link mx-2" href="https://www.linkedin.com/in/akash-gupta-9a2461228">Follow Us</a>
              </li>
              <li className="nav-item dropdown">
                <a className="nav-link dropdown-toggle mx-2" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  <i className="fa fa-filter"></i> Date Filter
                </a>
                <ul className="dropdown-menu">
                  <FilterByDate FromHeaderForDateFilter={FilterDateAndSaveInHeader} />
                </ul>
              </li>
              
            </ul>

            <Theme FromHeader={SaveThemeInHeader} defaultHeaderTheme={props.defaultTheme} />
            <FilterExpenses FromHeader={FilterAndSaveInHeader} searchingTitle={props.searchingTitleFromApp} />
          </div>
        </div>
      </nav>
    )
  }
  else return (
    <nav className="navbar navbar-expand-lg bg-dark fixed-top navbar-dark">
      <div className="container-fluid">
        <a className="navbar-brand" href="#">Kharcha-Pani Tracker</a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item mx-2">
              <a className="nav-link active" aria-current="page" href="#">Home</a>
            </li>
            <li className="nav-item mx-2" >
              <a className="nav-link" href="https://www.linkedin.com/in/akash-gupta-9a2461228">Follow Us</a>
            </li>
            <li className="nav-item dropdown mx-2">
              <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                <i className="fa fa-filter"></i> Date Filter
              </a>
              <ul className="dropdown-menu">
                <FilterByDate FromHeaderForDateFilter={FilterDateAndSaveInHeader} />
              </ul>
            </li>
           
          </ul>

          <Theme FromHeader={SaveThemeInHeader}  defaultTheme={props.defaultTheme }  />
          <FilterExpenses FromHeader={FilterAndSaveInHeader} searchingTitle={props.searchingTitleFromApp} />
        </div>
      </div>
    </nav>
  );
}
export default Header;