import React from "react";
import Chart from 'react-apexcharts';
import './PieChart.css';

function PieChart(props) {
    const obj = [props.expenseAmount, props.incomeAmount];
    return (
        <React.Fragment>
            <div className='chartClass'>
                
                <Chart className='chart'
                    type="donut"
                    
                    series={obj}

                    options={{
                        labels: ['Total Expense', 'Total Income'],
                        colors: [' rgb(209, 19, 19)', ' rgb(23, 196, 23)'],
                        title: {
                            text: "Total Income and Expense",
                            offsetX: 60,
                            style: {
                                fontSize:  '18px',
                                fontWeight:  'bold',
                                fontFamily: 'Segoe UI',
                              
                                color: 'var(--bgcolvar)',
                                
                              },
                            
                            
                            colors:'#f90000',
                        },
                        plotOptions: {
                            pie: {
                                donut: {
                                    labels: {
                                        show: true,
                                        total: {
                                            show: true,
                                           
                                            //    showAlways:true,
                                            //formatter: () => '343',
                                            fontSize: 17,
                                            fontWeight:'bold',
                                            color: 'var(--bgcolvar)',
                                            fontFamily: 'Segoe UI',
                                            

                                        }
                                    }
                                }
                            }

                        },
                        dataLabels: {
                            // enabled:false,
                        }


                    }}

                />

            </div>
        </React.Fragment>
    );
}
export default PieChart;