import React, { useState } from "react";
import './FilterByDate.css';

const FilterByDate = (props) => {
  let today_date = `${new Date().getFullYear()}-${new Date().toLocaleString("en-india", { month: "2-digit", })}-${new Date().getDate()}`;
  const [enteredDate1, setenteredDate1] = useState(today_date);
  const [enteredDate2, setenteredDate2] = useState(today_date);


  const dateChangeHandler1 = (e) => {
    setenteredDate1(e.target.value);
  };
  const dateChangeHandler2 = (e) => {
    setenteredDate2(e.target.value);
  };



  const submitHandler = (e) => {
    e.preventDefault();
    if(new Date(enteredDate1)<=new Date(enteredDate2))
      props.FromHeaderForDateFilter(enteredDate1, enteredDate2);
    else alert(`Starting date can't be more than ending date.`);
  };

  return (

    <form onSubmit={submitHandler} className='DateFilterForm'>
      <div>

        <div  className="d-flex ">
          <div className="mt-3 dateSelect" >From:</div>
          <div className='filter-expenses m-2'> <input type="date" className="filter-field input-field  px-2"
            value={enteredDate1}
            onChange={dateChangeHandler1} />
          </div>
        </div>

        <div className="d-flex">
        <div className="mt-3 mx-2 dateSelect">To:</div>
        <div className='filter-expenses m-2'> <input type="date" className="filter-field input-field px-2" value={enteredDate2}
          onChange={dateChangeHandler2} />
        </div>
       </div>
      </div>
      <button className="applyFilter" title="Apply Filter"><i className="material-icons filterIcon">send</i></button>
    </form>

  );
}

export default FilterByDate;