import "./Theme.css"
import { useState,useEffect } from "react";

const Theme = (props) => {

    const [themeClaases, setThemeClasses] = useState('theme-btn-light');
 
    const ThemeClickHandler = () => {
        
        props.FromHeader();
       
    }

    useEffect(() => {
  
        if (props.defaultTheme === 'dark-theme')
        setThemeClasses('theme-btn-dark')
    else setThemeClasses('theme-btn-light');
       
      }, [props.defaultTheme])
   
  
 if(themeClaases=='theme-btn-light')
 {
    return (
        <div className="themeContainer" >
            <button title="Change theme" className={themeClaases} onClick={ThemeClickHandler}><i className="fa fa-sun-o themeIconSun" ></i></button>
        </div>      
        );
    }
 else {
    return (
        <div className="themeContainer" >
             <button title="Change theme" className={themeClaases} onClick={ThemeClickHandler}><i className="fa fa-moon-o themeIconMoon" ></i></button>
        </div>      
        );
    }
    
}
export default Theme;
