import './TotalExpense.css' ;
import Card from '../UI/Card';

const TotalExpense = (props) => {

    
   const  title=props.title
   if(title=='Total Expense')
   return (
       <Card className='total-item-expense'>     
         <h3 className="total-item-expense__discription">{title}</h3>
         <Card className="total-item-expense__price"> {props.amount} &#8377;</Card>
   </Card>
       );
   else return (
       <Card className='total-item-income'>     
         <h3 className="total-item-income__discription">{title}</h3>
         <Card className="total-item-income__price"> {props.amount} &#8377;</Card>
   </Card>
   )
}

export default TotalExpense;