import './App.css'
import React, { useState, useEffect } from "react";
import Expenses from "./Components/Expenses/Expenses";
import NewExpense from "./Components/NewExpense/NewExpense";
import NoResultFound from './Components/Expenses/NoResultFound'
import DarkThemeParticle from './Components/DarkThemeParticle';
import Header from './Components/Header'
import LightThemeParticle from './Components/LightThemeParticle';
import Footer from './Components/Footer';
import { type } from '@testing-library/user-event/dist/type';


let updatedExpenses;

const getLocalItem = () => {
  let AppExpensesDataInLocalStrogeKey = localStorage.getItem('AppExpensesDataInLocalStrogeKey');
  if (AppExpensesDataInLocalStrogeKey)
    return JSON.parse(AppExpensesDataInLocalStrogeKey);
  return [];
}
const getLocalTheme = () => {
  let AppExpensesThemeInLocalStrogeKey = localStorage.getItem('AppExpensesThemeInLocalStrogeKey');
  if ( AppExpensesThemeInLocalStrogeKey==null)
    return 'light-theme';
 
  return AppExpensesThemeInLocalStrogeKey;
}




const App = () => {
  const [FilterUpdatedExpenses, setFilterUpdatedExpenses] = useState([]);
  const [totalAmount, setTotalAmount] = useState(0);
  const [totalIncome, setTotalIncome] = useState(0);
  const [AppExpenses, setAppExpenses] = useState(getLocalItem());
  const [AppTheme, setAppTheme] = useState(getLocalTheme());
  const [SearchingResult, setSearchingResult] = useState('');
  const [DateFilter, setDateFilter] = useState(0);

  const SaveThemeInApp = () => AppTheme === "light-theme" ?
    setAppTheme("dark-theme") : setAppTheme("light-theme");
  useEffect(() => {
    document.body.className = AppTheme;
  }, [AppTheme]);

  
  const backButtonHandler = () => {
    setSearchingResult('');
    setDateFilter(0);
  }

  const SaveInApp = (expenseObject) => {
    updatedExpenses = [expenseObject, ...AppExpenses];
    setAppExpenses(updatedExpenses);

  };

  useEffect(() => {
    let texpense = 0;
    for (let i = 0; i < AppExpenses.length; i++) {
      
  
      if (AppExpenses[i].category == 'Expense')
      texpense += Number(AppExpenses[i].amount);
    }
    setTotalAmount(texpense);
  }, [AppExpenses]);

  useEffect(() => {
    let tincome = 0;
    for (let i = 0; i < AppExpenses.length; i++)
    {
      if (AppExpenses[i].category == 'Income')
        tincome += Number(AppExpenses[i].amount);
      }
    setTotalIncome(tincome);
  }, [AppExpenses]);


  const SaveInAppToExpense = (deleteExpense) => {
    updatedExpenses = AppExpenses.filter(Expense => Expense.id !== deleteExpense.id);
    setAppExpenses(updatedExpenses);
    updatedExpenses = FilterUpdatedExpenses.filter(Expense => Expense.id !== deleteExpense.id);
    setFilterUpdatedExpenses(updatedExpenses);
  };

  const FilterAndSaveInApp = (filterExpenseName) => {
    setSearchingResult(filterExpenseName);
    updatedExpenses = AppExpenses.filter(Expense => Expense.title === filterExpenseName.toLocaleUpperCase());
    setFilterUpdatedExpenses(updatedExpenses);

  }


  const FilterDateAndSaveInApp = (startDate, endDate) => {
   
    setDateFilter(1);
    
    updatedExpenses = AppExpenses.filter(Expense => ((new Date(Expense.date) >= new Date(startDate)) && (new Date(Expense.date) <= new Date(endDate))));
    setFilterUpdatedExpenses(updatedExpenses);
  }

  useEffect(() => {
    localStorage.setItem('AppExpensesDataInLocalStrogeKey', JSON.stringify(AppExpenses));
  }, [AppExpenses]
  );
  useEffect(() => {
    localStorage.setItem('AppExpensesThemeInLocalStrogeKey', AppTheme);
  }, [AppTheme]
  );



 


  if (AppTheme === "light-theme") {
   

    if (SearchingResult !== '' || DateFilter) {
      let texp = 0,tinc=0;
      if (FilterUpdatedExpenses.length === 0)
        return (
          <div>
            <LightThemeParticle />
            <Header FromApp={SaveThemeInApp} FromAppForFilter={FilterAndSaveInApp} FromAppForDateFilter={FilterDateAndSaveInApp} searchingTitleFromApp={SearchingResult} defaultTheme={AppTheme} />
            <div className='backAndTheme'>
              <button onClick={backButtonHandler} title='Back'><i className="fa fa-arrow-circle-left back-btn-icon"></i></button>
            </div>
            <NoResultFound />
          </div>
        )
  
  
      for (let i = 0; i < FilterUpdatedExpenses.length; i++)
      {
        if(FilterUpdatedExpenses[i].category=='Expense')
          texp += Number(FilterUpdatedExpenses[i].amount);
        else tinc+= Number(FilterUpdatedExpenses[i].amount);
      }
      
       
  
  
      return (
        <div className='page-container'>
          <LightThemeParticle />

          <Header FromApp={SaveThemeInApp} FromAppForFilter={FilterAndSaveInApp} FromAppForDateFilter={FilterDateAndSaveInApp} searchingTitleFromApp={SearchingResult} defaultTheme={AppTheme} />
          <div className='backAndTheme'>
            <button onClick={backButtonHandler} className='back-btn' title='Back'><i className="fa fa-arrow-circle-left back-btn-icon"></i></button>
          </div>
          <Expenses Expenses={FilterUpdatedExpenses} FromAppToExpense={SaveInAppToExpense} ExpensesTheme={AppTheme} totalExpense={texp} totalIncome={tinc} />
        </div>
      );
      
    }
   
    else {
      
     
      return (
        <div className='page-container'>
          <LightThemeParticle />
          <Header FromApp={SaveThemeInApp} FromAppForFilter={FilterAndSaveInApp} FromAppForDateFilter={FilterDateAndSaveInApp} searchingTitleFromApp='Search Income/Expense' defaultTheme={AppTheme} />
          <NewExpense FromApp={SaveInApp} />
          
          <Expenses Expenses={AppExpenses} FromAppToExpense={SaveInAppToExpense} ExpensesTheme={AppTheme} totalExpense={totalAmount} totalIncome={ totalIncome} />
          <Footer />
        </div>
      );
    }



   
  }

   
else {
  if (SearchingResult !== '' || DateFilter) {
    let texp = 0,tinc=0;
    if (FilterUpdatedExpenses.length === 0)
      return (
        <div>
          <DarkThemeParticle />
          <Header FromApp={SaveThemeInApp} FromAppForFilter={FilterAndSaveInApp}  FromAppForDateFilter={FilterDateAndSaveInApp} searchingTitleFromApp={SearchingResult} defaultTheme = { AppTheme }/>
          <div className='backAndTheme'>
            <button onClick={backButtonHandler} title='Back'><i className="fa fa-arrow-circle-left back-btn-icon"></i></button>
          </div>
          <NoResultFound />
        </div>
      )


      for (let i = 0; i < FilterUpdatedExpenses.length; i++)
      {
        if(FilterUpdatedExpenses[i].category=='Expense')
          texp += Number(FilterUpdatedExpenses[i].amount);
        else tinc+= Number(FilterUpdatedExpenses[i].amount);
      }

    return (
      <div>
        <DarkThemeParticle />
        <Header FromApp={SaveThemeInApp}  FromAppForFilter={FilterAndSaveInApp}  FromAppForDateFilter={FilterDateAndSaveInApp} searchingTitleFromApp={SearchingResult} defaultTheme = { AppTheme } />
        <div className='backAndTheme'>
          <button onClick={backButtonHandler} className='back-btn' title='Back'><i className="fa fa-arrow-circle-left back-btn-icon"></i></button>
        </div>
        <Expenses Expenses={FilterUpdatedExpenses} FromAppToExpense={SaveInAppToExpense} ExpensesTheme={AppTheme} totalExpense={texp} totalIncome={tinc}/>
      
      </div>
    );
  }
  else return (
    <div className='page-container'>
       <DarkThemeParticle/>
        <Header FromApp={SaveThemeInApp} FromAppForFilter={FilterAndSaveInApp}   FromAppForDateFilter={FilterDateAndSaveInApp} searchingTitleFromApp='Search Income/Expense' defaultTheme = { AppTheme } />
        <NewExpense FromApp={SaveInApp} />
        <Expenses   Expenses={AppExpenses} FromAppToExpense={SaveInAppToExpense} ExpensesTheme={AppTheme} totalExpense={totalAmount} totalIncome={ totalIncome}/>
        <Footer/>
      </div>
  );
}


};
export default App;
